.tituloArtistas{
    position: absolute;
    padding-top: 2vw;
    font-size: 5vw;
    width: 72%;
    top: 1vw;
    color: white;
    background-color: #ED2A45;
    width: 100%;
    font-weight: bolder;
}

.ArtistasConfirmados_imagens {
    margin-top: 10vw;
    position: relative;
    top: vw;
    margin-left: 2.2%;
    margin-right: 2.2%;
    align-content: center;
    justify-content: center;
    align-items: center;
    text-align: center;
    display: flex;
}

.ArtistasConfirmados_imagens img {
    cursor: pointer;
    margin-top: 3vw;
    border-radius: 1vw;
    background-color: #ED2A45;
    opacity: 0.9;
}

#the-rock:hover,
#fabio-melo:hover,
#ivete-sangalo:hover,
#gil-vigor:hover {
    transition: 1s;
    margin: 2%;
    opacity: 1;
}

@media (max-width: 1100px) {

    .tituloArtistas {
        position: absolute;
        font-size: 55px;
        top: 0vw;
        font-weight: bolder;
    }

    .ArtistasConfirmados_imagens img {
        opacity: 1;
    }

    .ArtistasConfirmados_imagens {
        top: 20vw;
        position: relative;        
        margin-top: 40vw;
        display: list-item;
        margin-left: auto;
        margin-right: auto;
        list-style: none;
        
    }
    #the-rock,
    #fabio-melo,
    #ivete-sangalo,
    #gil-vigor {
        opacity: 1;
    }
}
.navItem a {
    text-decoration: none;
    font-family: "Roboto", sans-serif;
    color: black;
    font-size: 1.5vw;
    margin-right: 30px;
    font-weight: bolder;
    transition: 1s;
    text-transform: uppercase;
}

.navItem a:hover {
    color: #fdc41f;
    text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.479);
    transform: scale(1.08);
}

.logo {
    font-family: 'Bebas Neue', cursive;
    font-size: 2vw;
    transition: 1s;
    vertical-align: top;
}

.logo img {
    width: 3.5vw;
}

.logo:hover{
    color: white;
    transform: scale(1.06);
    text-decoration: none;
}

@media (max-width: 768px) {

    .logo{
        font-size: 10vw;
    }
    .logo img{
        width:13vw;
    }
    .navItem a {
        font-size: 5vw;
    }
}

