#local {
  position: relative;
  top: -9vw;
}

.localizacao{
  background-color: white;
  padding-top: 5vw;
  margin-top: -1vw  ;
}

.tituloLocal {
  position: absolute;
  font-size: 11vw;
  margin-left: 45%;
  width: 50%;
}

.tituloLocal::before{
  content: url(/landingpage-react/static/media/room_black_24dp.c0bb88c8.svg);
}

.subtituloLocal {
  position: absolute;
  margin-left: 45%;
  width: 50%;
  color: black;
  font-family: 'Dancing Script', cursive;
  font-style: italic;
  margin-top: 10vw;
  text-align: center;
  font-size: 3.5vw;
}

.mapa {
    display: flex;
    margin-bottom: 30px;
    margin-top: 30px;
    z-index: 1;
  }

  .mapa iframe {
    width: 30vw;
    height: 30vw;
    margin-left: 10%;
    border: 1px  solid black;
  }

  .localizacao a button{
    position: relative;
    margin-left: 58%;
    top: -5vw;
    width: 25%;
    border-radius: 50vw;
    padding: 1vw 5vw;
    color: #EFBB40;
    background-color: #ED2A45;
    font-family: 'Dancing Script', cursive;
    font-weight: bolder;
    font-size: 2.5vw ;
    transition: 1s;
    border: black solid 1px;
  }

  .localizacao a button:hover{
    transform: scale(1.1);
    color: black;
    border: black solid 1px;
    background-color: white;
  }

  .localizacao p {
    text-align: center;
    margin-top: 2vw;
    font-family: 'Lato';
    font-size: 1.2vw;
    font-style: italic;
    padding-top: 1vw;
    padding-bottom: 2vw;
    color: black;
    font-weight: bolder;
    }

  @media (max-width: 1000px) {

    .localizacao{
      padding-top: 1vw;
    }

    .tituloLocal {
      position: relative;
      margin-left: 15%;
      width: 60%;
      font-size: 65px;
    }

    .tituloLocal::before{
      content: none;
    }

    .localImg{
      content: url(/landingpage-react/static/media/room_black_24dp-2.08e62f99.svg);
      position: absolute;
      width: 20%;
      margin-left: 40%;
    }
  
    .subtituloLocal {
      position: relative;
      margin-left: 15%;
      width: 70%;
      color: black;
      margin-top: 7vw; 
      top: -12vw;
      text-align: center;
      font-weight: bolder;
      font-size: 25px;
    }
    .mapa iframe {
      position: relative;
      top: -20vw;
      margin-left: 10vw;
      height:80vw;
      width: 80vw;
    }
    
    .localizacao a button{
      position: absolute;
      margin-left: 20%;
      margin-top: -18vw;
      border-radius: 500px;
      width: 60%;
      font-size: 40px ;
      transition: 1s;
    }
    
  .localizacao p {
    font-size: 3vw;
    width: 100%;
    padding: 5vw 2vw;
    }
  }
 

